import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import BusinessDocumentTypesTable from './Tables/BusinessDocumentTypesTable';
import BusinessDocumentTypesModal from './Modals/BusinessDocumentTypesModal';
import BusinessTypeSearch from '../../SearchComponents/BusinessTypeSearch/BusinessTypeSearch';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';

class BusinessDocumentTypes extends React.Component {
    constructor(props, context) {
        super(props, context);
        var DropdownvalidationBusinessType = 0;
        var DropdownvalidationSubCounty = 0;
        this.state = {
            errors: {},
            columns: [],
            data: [],
            filteredData: [],
            filteredBusinessTypeData: [],
            filteredStatusData: [],
            value: '',
            show: false,
            showdelete: false,
            DOCUMENT_TYPE_ID: 0,
            DOCUMENT_TYPE_NAME: '',
            SET_BUSINESS_TYPE_ID: 0,
            SYS_STATUS_ID: 0,
            CREATEDBY: 'Sravan',
            LASTMODIFIEDBY: 'Sravan',
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            openbusinesstypesearch: false,
            openstatussearch: false,

            BUSINESSTYPEDESCRIPTION: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',

            searchcolumns: [],

            searchdata: [],
            searchbusinesstypedata: [],
            searchstatusdata: [],

            searchFilteredBusinessTypeData: [],
            searchFilteredStatusData: [],

            searchbusinesstypevalue: '',
            searchstatusvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowBusinessTypeSearch = () => {
            this.setState({ openbusinesstypesearch: true });
        };

        this.handleShowStatusSearch = () => {
            this.setState({ openstatussearch: true });
        };

        this.handleHideBusinessTypeSearch = () => {
            this.setState({ openbusinesstypesearch: false });
        };

        this.handleHideStatusSearch = () => {
            this.setState({ openstatussearch: false });
        };

        this.populateData();
        this.populateTypes();
        this.populateStatus();

    }

    handleValidation(){
        let errors = {};
        let formIsValid = true;

        if(!this.state.DOCUMENT_TYPE_NAME){
            formIsValid = false;
            errors["BusinessDocumentTypeName"] = "Please Enter Business Document Type Name";
        }

        if(!this.state.BUSINESSTYPEDESCRIPTION){
            formIsValid = false;
            errors["BusinessType"] = "Please select Busniess Type";
        }

        if(!this.state.BUSINESS_TYPE_ID)
        {
         formIsValid = false;
         errors["BusinessType"] = "Please select Busniess Type";
        }

        if(!this.state.SYSTEMSTATUSTYPEDESCRIPTION){
            formIsValid = false;
            errors["SystemStatus"] = "Please select System Status Type";
        }

        if(!this.state.SYS_STATUS_ID)
        {
         formIsValid = false;
         errors["SystemStatus"] = "Please select System Status Type";
        }

        if(this.DropdownvalidationBusinessType === 1)
        {
            errors["BusinessType"] = "";
            this.DropdownvalidationBusinessType = 0;
        }

        if(this.DropdownvalidationSystemStatus === 1)
        {
            errors["SystemStatus"] = "";
            this.DropdownvalidationSystemStatus = 0;
        }

        this.setState({errors: errors});
        return formIsValid;
    }

    populateData() {
        const getdata = `
            query{
                BusinessDocumentTypes{
                    DOCUMENT_TYPE_ID
                    DOCUMENT_TYPE_NAME                    
                    SET_BUSINESS_TYPE_ID   
                    BUSINESSTYPE{
                        BUSINESS_TYPE_ID
                        BUSINESS_TYPE_NAME
                    }                    
                    SYS_STATUS_ID
                    SYSTEMSTATUS{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.BusinessDocumentTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Business Document Types Data');
                }
            })

    }

    populateTypes() {
        const getdata = `
            query {
                BusinessTypes {
                    BUSINESS_TYPE_ID,
                    BUSINESS_TYPE_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchbusinesstypedata: data.data.BusinessTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Business Type Data');
                }
            })
    }

    populateStatus() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchstatusdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        if(this.handleValidation()){

            const form = `
            mutation{
                addBusinessDocumentType(
                    DOCUMENT_TYPE_NAME:"${this.state.DOCUMENT_TYPE_NAME}",                
                    SET_BUSINESS_TYPE_ID:${this.state.BUSINESS_TYPE_ID},                                        
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.LASTMODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        DOCUMENT_TYPE_ID
                }
            }
            `;

            const formEdit = `
            mutation{
                updateBusinessDocumentType(
                    DOCUMENT_TYPE_ID:${this.state.DOCUMENT_TYPE_ID},
                    DOCUMENT_TYPE_NAME:"${this.state.DOCUMENT_TYPE_NAME}",
                    SET_BUSINESS_TYPE_ID:${this.state.BUSINESS_TYPE_ID},                                        
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.LASTMODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        DOCUMENT_TYPE_ID
                }
            }
            `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteBusinessDocumentType(
                DOCUMENT_TYPE_ID:${this.state.DOCUMENT_TYPE_ID}
                ){
                    DOCUMENT_TYPE_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseBusinessTypeSearch = (e) => {

        this.setState({
            BUSINESS_TYPE_ID: e.BUSINESS_TYPE_ID,
            BUSINESSTYPEDESCRIPTION: e.BUSINESS_TYPE_NAME,
            openbusinesstypesearch: false,
        });

        this.DropdownvalidationBusinessType=1;
        this.DropdownvalidationSystemStatus=1;
        this.handleValidation();
    };

    handleCloseStatusSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            openstatussearch: false,
        });

        this.DropdownvalidationBusinessType=1;
        this.DropdownvalidationSystemStatus=1;
        this.handleValidation();
    };

    handleEditData = (e) => {
        this.setState({
            DOCUMENT_TYPE_ID: e.DOCUMENT_TYPE_ID,
            DOCUMENT_TYPE_NAME: e.DOCUMENT_TYPE_NAME,
            SET_BUSINESS_TYPE_ID: e.SET_BUSINESS_TYPE_ID,
            BUSINESSTYPEDESCRIPTION: e.BUSINESSTYPE.BUSINESS_TYPE_NAME,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYSTEMSTATUS.SYS_STATUS_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            DOCUMENT_TYPE_ID: e.DOCUMENT_TYPE_ID,
            DOCUMENT_TYPE_NAME: e.DOCUMENT_TYPE_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            DOCUMENT_TYPE_NAME: '',
            SET_BUSINESS_TYPE_ID: '',
            BUSINESSTYPEDESCRIPTION: '',
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.DOCUMENT_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.BUSINESSTYPE.BUSINESS_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.DOCUMENT_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.BUSINESSTYPE.BUSINESS_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchBusinessTypeFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchbusinesstypedata
        let filteredBusinessTypeData = this.state.searchFilteredBusinessTypeData
        this.setState({ searchbusinesstypevalue: value });

        if (value.length) {
            filteredBusinessTypeData = data.filter(item => {
                let startsWithCondition =
                    item.DOCUMENT_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.DOCUMENT_TYPE_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredBusinessTypeData })
        }
    }

    handleSearchStatusFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchstatusdata
        let filteredStatusData = this.state.searchFilteredStatusData
        this.setState({ searchstatusvalue: value });

        if (value.length) {
            filteredStatusData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredStatusData })
        }
    }

    changeEventandValidation = (e) => {
      
        if(e.target.name==="BusinessDocumentTypeName")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            DOCUMENT_TYPE_NAME:e.target.value
            });
        }

        if(e.target.name==="BusinessType")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            BUSINESSTYPEDESCRIPTION:e.target.value
            });
        }

        if(e.target.name==="SystemStatus")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SYSTEMSTATUSTYPEDESCRIPTION:e.target.value
            });
        }

        if(e.target.name==="BusinessTypeID")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            BUSINESS_TYPE_ID:e.target.value
            });
        }

        if(e.target.name==="SystemStatusID")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SYS_STATUS_ID:e.target.value
            });
        }        
         
    this.handleValidation();
   
};


    render() {

        const { data, columns, value, filteredData, filteredBusinessTypeData, filteredStatusData, searchbusinesstypevalue, searchstatusvalue, searchstatusdata, searchbusinesstypedata } = this.state

        return (
            <>
                <Helmet><title>Business Document Types</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Business Document Types"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Business Document Types</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <BusinessTypeSearch
                                        handleSearchBusinessType={this.handleCloseBusinessTypeSearch}
                                        opensearch={this.state.openbusinesstypesearch}
                                        handleHideSearch={this.handleHideBusinessTypeSearch}
                                        value={searchbusinesstypevalue}
                                        data={searchbusinesstypedata}
                                        handleFilter={this.handleSearchBusinessTypeFilter}
                                        filteredData={filteredBusinessTypeData}
                                    />

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseStatusSearch}
                                        opensearch={this.state.openstatussearch}
                                        handleHideSearch={this.handleHideStatusSearch}
                                        value={searchstatusvalue}
                                        data={searchstatusdata}
                                        handleFilter={this.handleSearchStatusFilter}
                                        filteredData={filteredStatusData}
                                    />

                                    <BusinessDocumentTypesTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <BusinessDocumentTypesModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowBusinessTypeSearch={this.handleShowBusinessTypeSearch}
                                        handleShowStatusSearch={this.handleShowStatusSearch}

                                        BusinessDocumentTypeName={this.state.DOCUMENT_TYPE_NAME}
                                        changeBusinessDocumentTypeName={e => this.setState({ DOCUMENT_TYPE_NAME: e.target.value })}

                                        BusinessTypeID={this.state.SET_BUSINESS_TYPE_ID}
                                        handleChangeBusinessTypeID={e => this.setState({ SET_BUSINESS_TYPE_ID: e.target.value })}

                                        BusinessTypeDescription={this.state.BUSINESSTYPEDESCRIPTION}
                                        handleChangeBusinessTypeDescription={e => this.setState({ BUSINESSTYPEDESCRIPTION: e.target.value })}

                                        SystemStatusTypeID={this.state.SYS_STATUS_ID}
                                        handleChangeSystemStatusTypeID={e => this.setState({ SYS_STATUS_ID: e.target.value })}

                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        handleChangeSystemStatusTypeDescription={e => this.setState({ SYSTEMSTATUSTYPEDESCRIPTION: e.target.value })}

                                        changeEventandValidation={this.changeEventandValidation}
                                        BusinessDocumentTypeNameError={this.state.errors["BusinessDocumentTypeName"]}
                                        BusinessTypeError={this.state.errors["BusinessType"]}
                                        SystemStatusTypeError={this.state.errors["SystemStatus"]}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(BusinessDocumentTypes));