import React from "react";
import { Button, Input } from "reactstrap";
import DataTable from "react-data-table-component";
import { Search } from "react-feather";
import { Modal } from 'react-bootstrap';


class BusinessTypeSearch extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                            onClick={() => this.props.handleSearchBusinessType(row)}
                        ><Search size={14} />
                        </Button.Ripple>
                    </>
                )
            },
            {
                name: "Business Type",
                selector: "BUSINESS_TYPE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.BUSINESS_TYPE_NAME}</p>
            }
        ]
    }
    render() {
        return (
            <>
                <Modal
                    show={this.props.opensearch}
                    onHide={this.props.handleHideSearch}
                >
                    <Modal.Header className="bg-info" closeButton>
                        <Modal.Title >
                            <Search /> Search Business Type
                    </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="position-relative has-icon-left mb-1">
                            <Input value={this.props.value} onChange={e => this.props.handleFilter(e)} />
                            <div className="form-control-position">
                                <Search size="15" />
                            </div>
                        </div>
                        <DataTable
                            className="dataTable-custom"
                            data={this.props.value.length ? this.props.filteredData : this.props.data}
                            columns={this.tableProperties.columns}
                            noHeader
                            pagination
                            striped
                        />
                    </Modal.Body>
                </Modal>
            </>
        )
    }
}

export default BusinessTypeSearch